html {
  background-color: lightsalmon; }

html, body {
  height: 100%; }

body,
.site {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column; }

ul {
  list-style-type: none; }

a {
  text-decoration: none; }

.site-header {
  background-color: orangered;
  color: white; }

.site-branding {
  float: left;
  width: 20%; }

.site-branding p {
  padding-bottom: 0.5rem; }

.navigation ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
  justify-content: space-around; }

.navigation ul > li > a {
  color: white; }

.site-content {
  padding: var(--spacing-site, 2em); }

.site-content {
  -webkit-box-flex: 1;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto; }

.site-content {
  margin: 0 auto; }

.site-content a {
  color: black; }

/*
.site-content ul li a{
    color: black;
}
*/
.site-footer {
  background-color: black;
  color: white;
  margin-top: 2rem; }

.site-footer ul > li > a {
  color: white; }

ul.posts {
  list-style-type: none;
  padding-left: 0; }

ul.posts li {
  display: block;
  -webkit-animation: slideIn 0.6s ease 1 both;
  animation: slideIn 0.6s ease 1 both;
  list-style: none outside;
  padding: 0.5rem 0; }

ul.posts li a {
  display: block;
  text-decoration: none;
  color: black; }

@-webkit-keyframes slideIn {
  from {
    opacity: 0;
    -webkit-transform: translateX(-5rem);
    transform: translateX(-5rem); }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0); } }

@keyframes slideIn {
  from {
    opacity: 0;
    -webkit-transform: translateX(-5rem);
    transform: translateX(-5rem); }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0); } }

ul.posts li:nth-child(2) {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s; }

ul.posts li:nth-child(3) {
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.6s; }

ul.posts li:nth-child(4) {
  -webkit-animation-delay: 0.9s;
  animation-delay: 0.9s; }

ul.posts li:nth-child(5) {
  -webkit-animation-delay: 1.2s;
  animation-delay: 1.2s; }

ul.posts li:nth-child(6) {
  -webkit-animation-delay: 1.5s;
  animation-delay: 1.5s; }

.why-choose-us {
  background: wheat;
  border-radius: 1%;
  margin: 0 auto;
  padding: 1rem 4rem; }

.why-choose-us h4 {
  text-align: center;
  padding-bottom: 1rem; }

.why-choose-us p {
  font-size: 0.8rem; }

.left_menu_cont {
  float: left;
  border-radius: 2rem;
  width: 25%;
  background: wheat;
  padding: 1rem; }

.left_menu_title {
  background: orangered;
  border-radius: 5rem;
  padding: 0.5rem;
  padding-top: 0.2rem; }

.left_menu_title a {
  font-size: 1.5rem; }

.right_menu_cont {
  float: right;
  width: 65%; }

.right_menu_cont h4 {
  text-align: left;
  margin-bottom: 2rem; }

p.site-description {
  font-size: 0.8rem; }

p.site-title {
  font-size: 1.2rem;
  font-weight: bold; }

p.site-title a {
  color: black; }

.newscats {
  display: none; }

.footer-links {
  float: right;
  margin-right: 2rem; }

.site-title img {
  height: 2rem;
  width: 2rem; }

/*Whitespace reset*/
* {
  padding: 0;
  margin: 0; }

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

/*typography*/
html {
  font: var(--font-size-initial, 14px)/1.5 "Verdana", sans-serif; }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--font-size-lv2, 16px); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-lv3, 18px); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-lv4, 21px); } }

body, table, td, th, textarea, option, select, form, input {
  font: inherit; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2; }

h1 {
  font-size: 3.998em; }

h2 {
  font-size: 2.827em; }

h3 {
  font-size: 1.999em; }

h4 {
  font-size: 1.414em; }

p, ul, ol {
  padding-bottom: 1.5em; }

ul, ol {
  padding-left: 1.5rem; }

li {
  list-style-position: inside; }

:root {
  --font-size-initial: 14px;
  --font-size-lv2: 16px;
  --font-size-lv3: 18px;
  --font-size-lv4: 21px; }
